@import "../../../utils/widthPicker";
@import "../../../utils/colorPalette";

.description {
  width: 660px;
  @include screen(greaterThanMedium) {
    margin-right: 30px; }
  @include screen(extraLarge) {
    width: 550px; }
  @include screen(large) {
    width: 410px; }
  @include screen(small) {
    width: 100%; } }

.image {
  width: 300px;
  height: 300px;
  border: 7px solid $oxidised-silver;
  filter: grayscale(85%);
  border-radius: 100%;
  transition: filter .2s;
  @include screen(lessThanMedium) {
    display: none; }
  @include screen(large) {
    width: 250px;
    height: 250px; }
  &:hover {
    filter: grayscale(0%); } }

.ruler {
  flex-grow: 1;
  margin-left: 30px;
  @include screen(small) {
    margin-left: 10px; } }

.heading {
  margin-bottom: 0;
  margin-left: 0; }

.titleRow {
  margin-bottom: 50px;
  align-items: center;
  @include screen(small) {
    margin-bottom: 10px; }
  @include screen(large) {
    margin-bottom: 35px; } }

.descriptionRow {
  justify-content: space-between;
  align-items: center; }

.listPadding {
  padding: 0;
  li {
    list-style: none; }
  li::before {
    content: "\003E";
    display: inline-block;
    font-size: 16px;
    width: 1em; }
  li a {
    color: $oxidised-silver;
    text-decoration: underline;
    text-decoration-style: dotted; } }
