@import "../../utils/widthPicker";

.imageDiv {
  display: flex;
  width: 400px;
  flex-direction: row-reverse;
  align-items: center;
  @include screen(small) {
    margin-left: 15px;
    margin-right: 0;
    margin-bottom: 10px;
    width: 100%; }
  @include screen(lessThanMedium) {
    flex-direction: row; }
  @include screen(medium) {
    width: 450px;
    margin-bottom: 3px;
    margin-right: 0; }
  @include screen(large) {
    width: 320px;
    margin-right: 12px; }
  @include screen(extraLarge) {
    margin-right: 20px; }
  @include screen(greaterThanExtraLarge) {
    margin-right: 40px;
    width: 500px; } }

.image {
  height: 90px;
  @include screen(small) {
    height: 50px; }
  @include screen(medium) {
    height: 60px; }
  @include screen(large) {
    height: 65px; }
  @include screen(greaterThanExtraLarge) {
    height: 100px; } }

.description {
  width: 450px;
  margin-bottom: 0;
  @include screen(small) {
    margin-left: 15px;
    margin-right: 15px;
    width: 100%; }
  @include screen(medium) {
    width: 450px; }
  @include screen(large) {
    width: 400px; } }

.contentDiv {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 60px;
  @include screen(greaterThanExtraLarge) {
    margin-right: 90px; }
  @include screen(small) {
    margin-top: 30px; }
  @include screen(medium) {
    margin-top: 30px; }
  @include screen(extraLarge) {
    margin-right: 40px; } }
