@import "../../../utils/widthPicker";

.blogShowcaseContainer {
  max-width: 960px;
  @include screen(greaterThanMedium) {
    padding: 0; }
  @include screen(greaterThanExtraLarge) {
    max-width: 1200px; } }

.blogPostCardsRow {
  justify-content: center; }

.blogShowcaseCard {
  display: none;
  @include screen(greaterThanExtraLarge) {
    display: block; } }
