@import "../../utils/widthPicker";

.point {
  cursor: pointer; }

.imageDiv {
  display: flex;
  width: 165px;
  align-items: center;
  margin-right: 25px;
  @include screen(large) {
    width: 110px;
    margin-right: 15px; }
  @include screen(small) {
    width: 58px;
    margin-right: 7px;
    margin-bottom: 5px; } }

.mobileDescription {
  display: none;
  @include screen(small) {
    display: block; } }

.desktopDescription {
  display: block;
  @include screen(small) {
    display: none; } }

.description {
  width: 225px;
  align-items: center;
  @include screen(greaterThanLarge) {
    width: 250px; }
  @include screen(small) {
    width: auto; }
  h4 {
    margin-bottom: 5px;
    font-size: 20px;
    @include screen(greaterThanLarge) {
      font-size: 22px; } }
  h6 {
    margin: 0;
    font-size: 14px;
    @include screen(greaterThanLarge) {
      font-size: 16px; } } }

.contentDiv {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  transition: transform 0.1s;
  &:hover {
    transform: scale(1.015); }
  &:active {
    transform: scale(1.005); }
  @include screen(medium) {
    margin: 10px 0; }
  @include screen(large) {
    margin: 10px 5px; }
  @include screen(greaterThanLarge) {
    margin: 10px 15px; }
  @include screen(greaterThanExtraLarge) {
    margin: 20px 30px; }
  @include screen(small) {
    margin: 10px 15px;
    justify-content: normal; } }
